@import url(https://fonts.googleapis.com/css?family=Assistant:600);
.login {
	background-color: #f5f7fa;
	font-size: 12px;
	color: #000;
	padding: 10px;
	margin-top: 10px;
	border-radius: 5px;
}

.login label {
	display: block;
	margin: 5px;
}

.login label span {
	display: block;
}

.login label input {
	width: 95%;
}

.login input[type=submit], .login button {
	margin: 5px;
	border: 1px solid black;
}
.date-range-form {
	display: inline;
}

.range-arrow, .refresh-button {
	vertical-align: middle;
	padding: 3px;
	color: inherit;
	font-size: inherit;
	background-color: transparent;
	border: 0;
}

.refresh-button[disabled] {
	color: #888;
}

.refresh-button:focus {
	outline: none;
}

.refresh-button.loading {
	-webkit-animation: spin 4s linear infinite;
	        animation: spin 4s linear infinite;
}

@-webkit-keyframes spin {
	from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
	to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}

@keyframes spin {
	from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
	to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}
.addon-logo {
	width: 72px;
	height: 72px;
}
.addon-sales-total {
	display: flex;
	overflow: hidden;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 150px;
	height: 150px;
	background: #fff;
	color: #5a5d9f;
	font-weight: bold;
	font-size: 24px;
	text-align: center;
	margin: 5px auto 10px;
	border-radius: 75px;
}
.addon-sales-subtotal {
	display: flex;
	flex-direction: column;
}
.addon-sales-subtotals {
	display: flex;
	text-align: center;
	border-top: 1px solid;
	padding: 5px;
	min-height: 40px;
	justify-content: space-around;
}
td {
	border-top: 1px solid;
}

td > .addon-sales-count, td > .addon-sales-amount {
	display: inline;
	padding-left: 1px;
}
.addon-sales-tier-name {
	text-align: left;
}

table {
	border-top: 1px solid;
	border-collapse: collapse;
	margin-bottom: 10px;
}

th, td {
	font-size: 10px;
	margin: 0;
	padding: 2px;
	text-align: right;
	width: 20%;
}
.addon-sales-platform {
	width: 100%;
	text-align: center;
	border-top: 1px solid;
}

.addon-sales-platform-totals {
	display: flex;
	justify-content: space-evenly;
	padding: 5px;
	font-size: 18px;
}
.addon-sales-platforms {
	display: flex;
	flex-wrap: wrap;
}
.addon-sales-card {
	margin: 10px;
	padding: 5px;
	color: #fff;
	text-shadow:  0 2px 3px rgba(0,0,0,.2);
	background: -webkit-linear-gradient(left,#79589f,#5a5d9f);
	background: linear-gradient(to right,#79589f,#5a5d9f);
	box-shadow: 0 2px 3px 2px rgba(0,0,0,.2);
	border-radius: 4px;
	width: 350px;
}

.addon-sales-card h1 {
	text-align: center;
	font-size: 16px;
}
.addon-sales-cards {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
	padding-top: 40px;
}
header {
	background: -webkit-linear-gradient(right,#79589f,#5a5d9f);
	background: linear-gradient(to left,#79589f,#5a5d9f);
	font-size: 16px;
	color: #fff;
	position: fixed;
	width: 100%;
	padding: 4px 0;
	border-bottom: 1px solid #f5f7fa;
}

header form {
	margin: 0 4px;
}

progress {
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;
	margin: 7px;
	height: 10px;
	float: right;
	width: 50px;
}

progress::-webkit-progress-bar {
	border-radius: 6px;
	background-color: white;
}

progress::-webkit-progress-value {
	border-radius: 6px;
	background-color: lightgrey;
}

.settings-menu {
	color: inherit;
	font-size: inherit;
	float: right;
	background-color: transparent;
	border: 0;
}
body {
	background-color: #f5f7fa;
  margin: 0;
  padding: 0;
  font-family: 'Assistant', sans-serif;
	font-size: 11px;
	box-sizing: border-box;
}
